import React from "react";
import "./style.css";

function Jumbotron() {
  return (
    <div className="jumbotron text-center">
      <h2>Kawaii Clicky Game</h2>
      <h4>Click any image to earn points --- but don't click any image twice!</h4>
    </div>
  );
}

export default Jumbotron;